            transform: scale(0.9, 0.9);
  }
}

@keyframes pulse {
  50% {
    -webkit-transform: scale(1.2, 1.2);
            transform: scale(1.2, 1.2);
  }
  100% {
    -webkit-transform: scale(0.9, 0.9);
