ul, li {
  list-style-type: none;
}

.todo__list {
  -ms-flex-positive: 1;
      flex-grow: 1;
  margin: 0;
  padding-left: 10px;
}

.todo__list-item {
  -ms-flex-align: center;
      align-items: center;
  display: -ms-flexbox;
  display: flex;
  margin-bottom: 1em;
}

@media (-moz-touch-enabled: 1), (pointer: coarse) {
  .todo__list {
    padding: 4em 0.4em 3em 0.4em;
  }
}

.todo__drag-handle {
  color: #ccc;
  cursor: ns-resize;
  margin-right: 0.6em;
}


